.result {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 15rem 5.7rem 6rem;
  width: 100%;

  @media (min-width: $stop-scaling) {
    padding: 150px 57px 60px;
  }

  @media (orientation: portrait) {
    padding: 8rem 0 4rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding: 8rem 0 3rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 3rem 0;
  }

  .form__button {
    color: transparent;
    animation: showButton 0.5s ease forwards 0.6s;
    &::before {
      background: none;
      border: 2px solid transparent;
      animation: showButtonPseudo 0.35s ease forwards 0.3s;
    }
  }

  .form__field {
    &::placeholder {
      color: transparent;
      transition: 1s all;
    }
    &_colored {
      &::placeholder {
        color: $c-dark;
      }
    }
  }
}

.result__image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: -1;

  @media (orientation: portrait) {
    left: -1rem;
    right: -1rem;
  }

  img {
    will-change: transform;
    position: absolute;
    animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }

  &-1 {
    bottom: 11rem;
    left: 53rem;
    width: 20rem;
    animation: fadeIn 1s forwards, resultImageAnimation-1 0.6s forwards, shaking 5s infinite 1.5s;


    @media (max-width: $mobile) {
      width: 15rem;
      left: auto;
    }
  }

  &-2 {
    bottom: 20rem;
    left: 74rem;
    width: 20rem;
    opacity: 0;
    animation: fadeIn 1s forwards 0.5s, resultImageAnimation-2 4s forwards 0.5s;
    transform-origin: bottom left;
    z-index: -1;

    @media (max-width: $mobile) {
      left: 20rem;
      bottom: 15rem;
      width: 15rem;
    }
  }

  &-3 {
    bottom: 15rem;
    left: 48rem;
    width: 20rem;
    opacity: 0;
    animation: fadeIn 1s forwards 0.5s, resultImageAnimation-2 4s forwards 0.4s;
    transform-origin: bottom right;
    z-index: -1;


    @media (max-width: $mobile) {
      left: auto;
      right: 18rem;
      bottom: 15rem;
      width: 15rem;
    }
  }

  &-4 {
    bottom: 18rem;
    left: 73rem;
    width: 30rem;
    opacity: 0;
    animation: fadeIn 1s forwards 0.5s, resultImageAnimation-2 4s forwards 0.3s;
    transform-origin: bottom left;
    z-index: -1;

    @media (max-width: $mobile) {
      left: 15rem;
      bottom: 15rem;
      width: 20rem;
    }
  }

  &-5 {
    bottom: 11rem;
    left: 55rem;
    width: 30rem;
    animation: fadeIn 1s forwards, resultImageAnimation-3 0.6s forwards;
    z-index: -1;


    @media (max-width: $mobile) {
      width: 25rem;
      left: auto;
    }
  }
}

.result__title {
  margin: 0 auto 2rem;
  color: $c-light;

  @media (min-width: $stop-scaling) {
    margin: 0 auto 20px;
  }

  @media (orientation: portrait) {
    margin-bottom: 1.5rem;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
  }
}

.result__text {
  margin-bottom: 2rem;
  text-align: center;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.01em;
  transform: scale(1.5);
  opacity: 0;
  animation: fadeIn 0.6s ease forwards, riseDown 0.6s ease forwards;

  @media (min-width: $stop-scaling) {
    margin-bottom: 20px;
    font-size: 48px;
  }

  @media (orientation: portrait) {
    font-size: 2.8rem;
    line-height: 1.1;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 2.8rem;
  }

  p {
    margin: 0;
  }
}

.result__button {
  @include btn-reset;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  margin: auto auto 1rem;
  min-width: 34rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
  color: $c-light;

  @media (min-width: $stop-scaling) {
    margin: auto auto 10px;
    min-width: 340px;
    font-size: 24px;
  }

  @media (orientation: portrait) {
    margin: auto 0 0;
    transform: none;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    transform: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    margin: auto 0 0;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }

  span {
    position: relative;
    margin-left: 1.5rem;
    width: 4rem;
    height: 4rem;
    color: $c-dark;

    @media (min-width: $stop-scaling) {
      margin-left: 15px;
      width: 40px;
      height: 40px;
    }

    &::before {
      @include center;
      content: "";
      width: 100%;
      height: 100%;
      background-color: $c-light;
      border: 2px solid $c-light;
      border-radius: 50%;
      z-index: -1;
    }
  }

  svg {
    @include center;
    margin-left: 0.3rem;
    width: 2.4rem;
    height: 1.9rem;

    @media (min-width: $stop-scaling) {
      margin-left: 3px;
      width: 24px;
      height: 19px;
    }
  }

  &:hover {
    span::before {
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    outline: none;

    span::before {
      border-color: $c-purple;
    }
  }

  &:active,
  &:active:focus {
    span {
      color: $c-light;
    }

    span::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;
    }
  }
}

.result__form {
  margin-top: auto;
}

.result--trip,
.result--prize {
  .result__title {
    width: 57.3rem;
    height: 11.3rem;
    transform: scale(1.5);
    opacity: 0;
    animation: fadeIn 0.6s ease forwards, riseDown 0.6s ease forwards;

    @media (min-width: $stop-scaling) {
      width: 573px;
      height: 113px;
    }

    @media (orientation: portrait) {
      width: 28.7rem;
      height: 5.7rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      width: 40rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin-bottom: 0;
      width: 35rem;
    }
  }
}

.result--negative {
  .result__title {
    margin-top: 9.5rem;
    width: 66.5rem;
    height: 11.3rem;

    @media (min-width: $stop-scaling) {
      margin-top: 95px;
      width: 665px;
      height: 113px;
    }

    @media (orientation: portrait) {
      margin-top: 0;
      width: 28.8rem;
      height: 4.9rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      margin-top: 0;
      width: 50rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 45rem;
    }

    img {
      position: relative;
      top: -50%;
      height: 200%;
    }
  }

  img {
    top: 0;
    left: 0;
    width: 100%;
  }

  .result__button {
    opacity: 0;
    animation: fadeIn 1s ease forwards 1s;

    span {
      opacity: 0;
      animation: resultNegativeButton 0.8s linear forwards 1s, fadeIn 1s forwards ease;
    }
  }
}
