.game__title {
  margin: 0;
  text-transform: uppercase;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.04em;
}
@media (min-width: 2000px) {
  .game__title {
    font-size: 48px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .game__title {
    margin-bottom: 1.8rem;
    font-size: 3.6rem;
